import logo from './logo.svg';
import './App.css';
import PlayerTile from './PlayerTile.js';
import React from 'react';
import cloneDeep from 'lodash/cloneDeep';

function getStartState () {
    const startState = {
        past: {
            past: 0,
            state: 0
        },
        present: {
            players: [
                {"id": "1", "name": "", "status": "active"},
                {"id": "2", "name": "", "status": "active"},
                {"id": "3", "name": "", "status": "active"},
                {"id": "4", "name": "", "status": "active"},
                {"id": "5", "name": "", "status": "active"},
                {"id": "6", "name": "", "status": "active"},
                {"id": "7", "name": "", "status": "active"}
            ],
            matchHistory: []
        }
    };
    return cloneDeep(startState);
}


function UndoButton(props) {
    if (props.past === 0) {
        return <button disabled>Undo</button>
    } else {
        return <button onClick={props.handleUndo}>Undo</button>
    }
}

class App extends React.Component {
  constructor(props) {
    super(props)
    this.handleMatch = this.handleMatch.bind(this);
    this.handleEliminate = this.handleEliminate.bind(this);
    this.handleUndo = this.handleUndo.bind(this);
    this.handleText = this.handleText.bind(this);
    this.handleReset = this.handleReset.bind(this);

    this.state = getStartState();
  }

  handleText(e) {
    const players = this.state.present.players
    players[e.target.id - 1].name = e.target.value
    this.setState({
        players: players
    });
  }

  handleReset(e) {
    const s = getStartState();

    this.setState({
        past: {
            past: s.past.past,
            state: s.past.state
        },
        present: {
            players: s.players,
            matchHistory: s.matchHistory
        }
    });
    console.log(this.state);
  }

  handleMatch(e) {
    const players = this.state.present.players
    const snapshot_past = cloneDeep(this.state.past)
    const snapshot_present = cloneDeep(this.state.present)
    players[e.target.id - 1].status="matched"
    this.setState({
        past: {
            past: snapshot_past,
            state: snapshot_present
        },
        players: players
    });
  }

  handleEliminate(e) {
    const players = this.state.present.players
    const snapshot_past = cloneDeep(this.state.past)
    const snapshot_present = cloneDeep(this.state.present)
    players[e.target.id - 1].status="eliminated"
    players.forEach((p) => {
        if (p.status == "matched") {
            p.status = "active";
        }
    })

    this.setState({
        past: {
            past: snapshot_past,
            state: snapshot_present
        },
        players: players
    });
  }

  handleUndo(e) {
    const past = cloneDeep(this.state.past)
    const pastpast = cloneDeep(this.state.past.past)
    console.log(past)
    this.setState({
        past: pastpast,
        present: {
            players: past.state.players,
            matchHistory: past.state.matchHistory
        }
    })
  }

  render() {
    const PlayersList = this.state.present.players.map((p) =>
      <PlayerTile 
        id={p.id} 
        status={p.status} 
        handleMatch={this.handleMatch} 
        handleEliminate={this.handleEliminate}
        handleText={this.handleText}
      />
    );
    return (
      <div className='App'>
        {PlayersList}
        <UndoButton
            handleUndo={this.handleUndo} 
            past={this.state.past.past}/>
        <button onClick={this.handleReset}>Reset</button>
      </div>

    );
  }
}

export default App;
