import React from 'react';

class PlayerTile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {value: ''};
  }

  render() {
    if (this.props.status === "eliminated") {
        return (
            <div className="PlayerTile">
                <input id={this.props.id} type="text" onChange={this.props.handleText}/>
                <button tabindex="-1" disabled>Match</button>
                <button 
                    tabindex="-1"
                    disabled>Eliminate
                </button>    
                {this.props.status}
            </div>
        )
    } else {
        return (
            <div className="PlayerTile">
                <input id={this.props.id} type="text" onChange={this.props.handleText}/>
                <button id={this.props.id} type="button" onClick={this.props.handleMatch} tabindex="-1">Match</button>
                <button 
                    id={this.props.id}
                    onClick={this.props.handleEliminate}
                    tabindex="-1">Eliminate</button>
                {this.props.status}
            </div>
        );
    }
  }
}

export default PlayerTile;
